}
@media screen and (max-width: 991px) {
  .header .header-container .burger-container {
    display: block;
  }
}
.header .header-container .burger-nav {
  position: relative;
  display: none;
}
@media screen and (max-width: 991px) {
  .header .header-container .burger-nav {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    position: absolute;
    top: 80px;
    left: -100%;
    transition: all 0.4s ease;
    background: #f5f6f6;
    box-shadow: #ccc;
    width: 100%;
    box-shadow: 0 4px 2px -2px rgba(36, 36, 36, 0.17);
    z-index: 5;
  }
}
.header .header-container .burger-nav.active {
  left: 0;
}
.header .header-container .burger-nav.active .login-btn {
  margin: 0 0 30px 30px;
  padding: 16px 37px;
  background: #1f4590;
  font-family: Mulish;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  color: #ffffff;
  border-radius: 4px;
}
.header .header-container .burger-nav.active .burger-nav-list .nav-items {
  margin-left: 30px;
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 36px;
  color: #000000;
}
.header .header-container .burger-menu {
  display: none;
}
@media screen and (max-width: 991px) {
  .header .header-container .burger-menu {
    display: block;
    position: relative;
    width: 50px;
    height: 40px;
  }
  .header .header-container .burger-menu:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    top: 0;
    left: 0;
    background: black;
    border-radius: 4px;
  }
  .header .header-container .burger-menu:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background: black;
    border-radius: 4px;
  }
  .header .header-container .burger-menu .burger-line {
    position: absolute;
    width: 100%;
    height: 2px;
    top: 50%;
    left: 0;
    background: black;
    border-radius: 4px;
  }
}
